import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
} from "@nextui-org/dropdown";
import { Avatar } from "@nextui-org/react";
import { DefaultSession } from "next-auth";
import { signOut } from "next-auth/react";
import Link from "next/link";
import { FC } from "react";
type Props = {
  session: DefaultSession | null;
};
const UserDropdown: FC<Props> = ({ session }) => {
  const onAction = (key: any) => {
    console.log(key);
    if (key == "logout") {
      signOut();
    }
  };

  return (
    <Dropdown placement="bottom-end">
      <DropdownTrigger>
        <Avatar
          isBordered
          as="button"
          className="transition-transform"
          src={
            session?.user?.image
              ? session?.user.image
              : "https://i.pravatar.cc/150?u=a042581f4e29026704d"
          }
        />
      </DropdownTrigger>
      <DropdownMenu
        aria-label="Profile Actions"
        variant="flat"
        disabledKeys={["profile"]}
        onAction={onAction}
      >
        <DropdownItem key="profile" className="h-14 gap-2">
          <p className="font-semibold">Iniciaste como:</p>
          <p className="font-semibold">{session?.user?.name || "Sin sesion"}</p>
        </DropdownItem>
        <DropdownItem key="settings" as={Link} href="/profile">
          Mi Perfil
        </DropdownItem>
        <DropdownItem key="logout" color="danger">
          Cerrar sesión
        </DropdownItem>
      </DropdownMenu>
    </Dropdown>
  );
};

export default UserDropdown;
