import {
  Navbar,
  NavbarBrand,
  NavbarContent,
  NavbarItem,
  NavbarMenuToggle,
  NavbarMenu,
  NavbarMenuItem,
} from "@nextui-org/navbar";
import { Link } from "@nextui-org/react";
import Image from "next/image";
import { useState } from "react";
import UserDropdown from "./UserDropdown";
import { ROUTES } from "@/routes";
import { RouteGroup, RouteNormal } from "./Sidebar";
import { useSession } from "next-auth/react";

const AcmeLogo = () => {
  return (
    <Link href="/">
      <Image src="/img/logo-inverse.svg" height={30} width={100} alt="logo" />
    </Link>
  );
};

const LayoutNavbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const { data: session, status } = useSession();

  return (
    <Navbar
      isMenuOpen={isMenuOpen}
      onMenuOpenChange={setIsMenuOpen}
      isBordered
      maxWidth="full"
    >
      <NavbarContent>
        <NavbarMenuToggle
          aria-label={isMenuOpen ? "Close menu" : "Open menu"}
          className="xl:hidden"
        />
        <NavbarBrand>
          <AcmeLogo />
        </NavbarBrand>
      </NavbarContent>

      <NavbarContent justify="end">
        <span className="capitalize">{session!.user.name}</span>
        <NavbarItem>
          <UserDropdown session={session} />
        </NavbarItem>
      </NavbarContent>

      <NavbarMenu>
        {ROUTES.map((route, index) => (
          <NavbarMenuItem key={route.key}>
            {route.children ? (
              <RouteGroup key={route.key} route={route} />
            ) : (
              <RouteNormal key={route.key} route={route} />
            )}
          </NavbarMenuItem>
        ))}
      </NavbarMenu>
    </Navbar>
  );
};

export default LayoutNavbar;
